import React from 'react'
import Slider from 'react-slick';
import banner1 from "../../assets/images/carousel/banner1.jpg"
import banner2 from "../../assets/images/carousel/banner2.jpg"
import "slick-carousel/slick/slick.css"; 
import "slick-carousel/slick/slick-theme.css";
import "./Carousel.css"


export default function Carousel() {
    const settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        arrows: true
    };
  return (
    <Slider className='sliderDiv' {...settings} >
    <div>
        <img src={banner1} alt="Banner 1" />
        <br/><br/><br/><br/>
    </div>
    <div>
        <img src="https://amritmahotsav.nic.in/writereaddata/Portal/Images/har-ghar-tiranga-web-banner-download-section-english-version.jpg" alt="Banner 2" />
    </div>
    {/* Add more slides as needed */}
</Slider>
  )
}
