import React from 'react';
import "./Header.css"
import logo from "../../assets/images/logo.png"

export default function Header() {
  return (
    <div className="header">
           <img src={logo} alt="Nagar Palika Parishad Logo" className="header-logo" />
            <div className="header-text">
                <div className="header-text-hindi">
                  ,  , 
                </div>
                <div className="header-text-english">
                    Nagar Palika Parishad, Khurai Dist, Sagar
                </div>
            </div>
        </div>
  )
}
